import "core-js/modules/es.function.name.js";
import { defineComponent, computed } from 'vue';
import { useStore } from '@/store';
import localCache from '@/utils/cache';
import { useRouter } from 'vue-router'; // import store from '@/store/index'

export default defineComponent({
  setup: function setup() {
    var store = useStore();
    var name = computed(function () {
      return store.state.login.userInfo.name;
    });
    var router = useRouter(); // 推出登录，清空本地存储，跳转到登录

    var handleExitClick = function handleExitClick() {
      localCache.deleteCache('token');
      router.push('/main');
    };

    return {
      name: name,
      handleExitClick: handleExitClick
    };
  }
});